#progress-tab-1.tab-pane ~ .tab-pane {
  display: none; }

.tab-list {
  @include f;
  @include position(relative, null null -1px null ); }

.tab-btn {
  @include padding(20px null );
  width: 200px;
  margin-right: 10px;

  text-align: center;
  letter-spacing: 1px;
  cursor: pointer;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  @include max(400) {
    @include padding(12px 20px );
    width: auto; } }


.tab-btn:not(.active) {
  color: #777;
  border: 1px solid #ddd;
  border-bottom-width: 0px;
  @include hoverBgc(#f7f7f7);
  @include hoverText(#000); }

.tab-btn.active {
  color: #000;
  background-color: #f7f7f7; }


.progress__controller {
  background-color: #fff;
  .row {
    height: 50px; }

  .wide10 {
    white-space: nowrap; }

  .wrapper {
    @include size(100%);
    position: relative; }

  .wrapper.select {
    margin-right: 10px; }

  .wrapper.input {
    margin-right: 15px; }

  select,
  input {
    @include size(100%);
    color: #777; }

  button {
    white-space: nowrap;
    @include size(195px, 100%);
    flex-shrink: 0;
    color: $orange;
    border-radius: 5px;
    border: 1px solid $orange;
    @include hoverBgc($orange);
    @include hoverText(#fff); }

  @include max(767) {
    .row {

      height: auto;
      flex-wrap: wrap;
      .wide10 {
        margin-bottom: 12px; }

      .wrapper,
      button {
        width: 100%;
        height: 45px; }
      .wrapper.select {
        @include margin(null 0 12px null ); } } } }




