.table.two-tone {
  text-align: center;

  thead {
    background-color: $orange;
    color: #fff; }

  tbody {
    background-color: #fff;
    font-size: 18px;
    color: #111; }

  td {
    border: 1px solid #ccc; }

  th:not(:last-child) {
    border-right: 1px solid #fff; } }

