 } }    //   display: block





.progress_checkin {
  label {
    display: flex; }
  .f18 {
    margin-right: 10px; }
  @include max(767) {
    .radio_unit {
      margin-bottom: 10px; }
    span {
      flex-wrap: wrap;
      .f15 {
        width: 100%;
        white-space: normal;
        color: #555;
        padding-left: 22px; } } } }


.progress_id {
  background-color: #fff;

  label {
    margin-right: 10px; }

  .radio_unit span:before {
    margin-right: 8px; }

  .mix {
    input {
      margin-right: 24px;
      max-width: 820px; } }

  @include min(500) {
    .mix span {
      white-space: nowrap; } }

  @include max(767) {
    .row {
      flex-wrap: wrap; }
    .mix input {
      margin-right: 0; } } }


.table.forth-type {
  th {
    @include padding(12px null ); }
  td {
    width: 25%;
    @include padding(14px null ); }

  @include min(1366) {
    .fool > div {
      width: 180px;
      @include margin(null auto);
      text-align: left; } }

  @include max(1200) {
    tbody {
      font-size: 16px; } } }

.table.forth-type {
  @include max(767) {
    text-align: left;
    tbody {
      font-size: 15px;
      .f20 {
        font-size: 15px; }
      line-height: 26px; }
    th {
      display: none; }

    tbody, tr, td {
      display: block; }
    tr {
      border: 1px solid $orange;
      border-top: 0px solid $orange; }
    td {
      position: relative;
      border: 0;
      @include padding(0px null );
      padding-left: 130px;
      white-space: nowrap;
      @include max(340) {
        &.fool > div {
          font-size: 13px; } } }

    td:before {
      content: attr(data-title);
      @include position(absolute, 0 null null 0);
      width: 120px;
      @include padding(null 10px);
      background-color: #f7f7f7; }
    td:nth-child(even):before {
      background-color: #ededed; } } }

