
main {
  opacity: 0;
  transition: opacity 0.5s;
  &.show {
    opacity: 1; } }

$orange-light: #fdecda;
// $orange: #F58729
$orange: rgb(242, 147, 5);
$orange-dark: #de5a12;

$green: #93B74B;
$blue: #0016F3;
$blue-ligh: #0071CC;
$footer: #423F3C;
$gray-bgc: #f7f7f7;
$red: #FF1010;


$defaultcolor: #333333 !default;


body {
  // font-size: 16px
  // color: $defaultcolor
  // font-family: 'Lato',"Open Sans", Verdana, Heiti TC, "微軟正黑體" , Microsoft JhengHei
  // font-family: "微軟正黑體" , Microsoft JhengHei, sans-serif, Verdana, Heiti TC,
  font-family: 'Noto Sans TC', sans-serif, Verdana, Heiti TC, "微軟正黑體" , Microsoft JhengHei; }

.default {
  color: $defaultcolor; }

.orange {
  color: $orange; }

.red {
  color: $red; }

.blue {
  color: $blue; }

.b {
  font-weight: bold;
 }  // font-weight: 500

.under {
  text-decoration: underline; }

.tc {
  text-align: center; }



.en {
  font-family: 'Philosopher'; }

.wp {
  background-color: #fff; }

a {
  color: $defaultcolor;
  transition: color .30s ease-in-out; }



select,
input,
button {
  @include padding(null 20px);
  background-color: #fff;

  @include max(768) {
    @include padding(null 12px); }

  @include max(450) {
    @include padding(null 8px); } }


.std {
  height: 45px;
  border: 1px solid #ccc;

  &::placeholder {
    color: #999; }

  &:hover,
  &:focus {
    border-color: $orange-dark; }

  @include max(768) {
    height: 40px;
    font-size: 15px; } }


.w100 {
  width: 100%; }

.row {
  @include fc(flex-start); }

.row.fc {
  justify-content: center; }

.btn-main {
  border: 1px solid $orange;
  background-color: #fff;
  color: $orange;

  min-width: 100px;
  @include padding(null 15px);
  @include min(768) {
    min-width: 190px; }


  @include hoverBgc($orange);
  @include hoverText(#fff); }


.btn {
  // color: $defaultcolor
  height: 45px;
  @include fc;
  display: inline-flex;
  border-radius: 5px;
  font-size: 18px;


  @include max(500) {
    font-size: 16px; } }


.section.gray {
  background-color: $gray-bgc; }


// .box-indent
//   +max(767)
//     +padding(null 0 )

.md-noPadding {
  @include max(768) {
    @include padding(null 0 ); } }
