
.symptom_index {
  background-color: #fff;
  border: 2px solid rgba($orange, 0.4);
  border-radius: 10px;

  .wrapper {
    @include f;
    flex-wrap: wrap;
    position: relative;
    overflow: hidden; }

  .bgc-line {
    width: 100%;
    border-bottom: 1px solid #ccc;
    @include position(absolute, null 0 null null ); }


  @for $var from 1 through 9 {
    @include min(991) {
      .bgc-line:nth-child(#{$var}) {
        top: #{70*$var}px; } }
    @include m(990,768) {
      .bgc-line:nth-child(#{$var}) {
        top: #{60*$var}px; } }
    @include max(767) {
      .bgc-line:nth-child(#{$var}) {
        top: #{45*$var}px; } } }

  a {
    width: calc(100%/7);
    line-height: 70px;
    color: #555;
    @include hoverText($orange); }

  a.active {
    color: $orange; }

  @include max(990) {
    a {
      width: calc(100%/6);
      line-height: 60px; } }

  @include max(767) {
    @include padding(null 10px);
    a {
      width: calc(100%/4);
      line-height: 45px;
      font-size: 16px;
      color: #111111; } }

  @include max(400) {
    @include padding(null 5px);
    a {
      width: calc(100%/3); } } }

.suggest {
  ul {
    background-color: #fff;
    color: #555555;
    padding-left: 90px;
    padding-right: 45px;
    text-indent: -36px;
    @include max(767) {
      padding-left: 70px;
      padding-right: 25px; }
    @include max(350) {
      padding-left: 30px;
      text-indent: 0px; } } }

.suggest_refer {
  .b {
    @include short-border(18px , #ccc);
    margin-right: 22px;
    padding-right: 22px;
    line-height: 2; }

  @include max(767) {
    margin-left: 0;
    .b {
      display: block;
      &:before {
        display: none; } } } }




#symptom_anchor,
#reference_anchor {
  transform: translateY(-94px); }
