@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC:100,400,500,700");
a {
  display: inline-block;
  white-space: nowrap;
  outline: none !important;
  cursor: pointer;
  text-decoration: none; }
  a:active {
    cursor: pointer;
    text-decoration: none; }

html, body {
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.wp {
  min-width: 320px;
  overflow: hidden; }

body {
  height: 100.1% !important;
  overflow-x: hidden;
  -webkit-backface-visibility: hidden; }

@media all and (max-width: 320px) {
  body, html, header {
    width: 320px !important; } }

body:after {
  display: block;
  content: "We're sorry! this website does not support your browser, please upgrade your browser, thank you.";
  position: fixed;
  z-index: 100000000000000;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  padding: 50px;
  background-color: #fff;
  text-align: center;
  font-size: 20px;
  color: #3f3f3f; }

:root body:after {
  display: none; }

h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
  line-height: normal; }

img {
  max-width: 100%;
  height: auto;
  border: none;
  -khtml-user-select: none;
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none; }

textarea {
  resize: none; }

::-webkit-input-placeholder, ::-moz-placeholder, ::-o-placeholder, ::-ms-placeholder, ::-ms-input-placeholder {
  color: #888888; }

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-transform: none; }

table {
  -webkit-backface-visibility: visible; }

button {
  border: none;
  outline: none !important;
  text-transform: none; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button, input {
  line-height: normal; }

button, html input[type="button"] {
  -webkit-appearance: button;
  cursor: pointer; }

figure, address {
  padding: 0;
  margin: 0; }

input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

input[type="search"] {
  -webkit-appearance: textfield;
  nowhitespace: afterproperty;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

button[disabled], html input[disabled] {
  cursor: default; }

input[type=search]::-ms-clear, input[type=search]::-ms-reveal {
  display: none;
  width: 0;
  height: 0; }

input[type="search"]::-webkit-search-decoration, input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-results-button, input[type="search"]::-webkit-search-results-decoration {
  display: none; }

input:focus,
select:focus,
textarea:focus {
  outline-color: transparent; }

input:focus,
select:focus,
textarea:focus {
  outline: none; }

.hidden {
  display: none; }

.scrollbar {
  -webkit-overflow-scrolling: touch; }

.scrollbarX {
  -webkit-overflow-scrolling: touch;
  overflow-x: auto; }

.scrollbarY {
  -webkit-overflow-scrolling: touch;
  overflow-y: auto; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.clearfix:after {
  display: block;
  content: '';
