


.curve {
  width: 160px;
  line-height: 45px;
  text-align: center;
  background-color: $orange;
  color: #fff;
  margin-left: 20px;
  border-radius: 99px; }


.department__doctor {
  @include f;
  align-items: center;
  border-radius: 8px;
  margin-bottom: 20px;

  .f20 {
    @include margin(null 35px null 25px);
    white-space: nowrap;
    // font-family: sans serif
 }    // letter-spacing: 2px

  .wrapper {
    @include size(100%);
    height: 45px;
    max-width: 335px;
    position: relative; }

  .wrapper.select {
    margin-right: 10px; }

  .wrapper.input {
    margin-right: 15px; }


  select,
  input,
  button {
    border: 0; }
  //   +padding(null 20px)

  select,
  input {
    @include size(100%);
    color: #777; }

  button {
    color: #fff;
    width: 100px;
    height: 45px;
    background-color: $orange; }


  @include max(767) {
    flex-wrap: wrap;
    .wrapper.select {
      max-width: 100%;
      @include margin(null 0 12px null); }
    .wrapper.input {
      max-width: calc(100% - 115px); }

    .f20 {
      @include margin(null null 12px 0); } } }

.department-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #fff;
  font-size: 18px;

  th,td {
    border-right: 3px solid #f7f7f7;
    padding: 0.75rem;
    font-weight: normal; }

  td {
    vertical-align: top; }

  tr:nth-child(even) {
    background-color: #f2f2f2; }

  tr.test {
    th, td {
      background-color: rgba($orange, 0.5);
      border-right-width: 0px;
      @include padding(3px null); } }


  thead {
    background-color: $orange;
    color: #ffffff;
    text-align: center; }

  tbody {
    tr {
      @include hoverBgc($orange-light); }
    th {
      color: #555;
      text-align: center; }
    td {
      padding-left: 35px; } }

  a {
    display: block;
    color: #737373;
    @include hoverText($orange); }

  .state-full {
    pointer-events: none; }

  .full {
    color: $red; }
  .teach,
  .first {
    color: $blue-ligh; }


  @include min(991) {
    .toggle .f14 {
      display: none; } }

  @include max(990) {
    &, tbody, thead, td, th {
      display: block; }
    & {
      position: relative; }
    thead {
      @include position(absolute, 0 null null 0);
      width: 100%;
      cursor: pointer;
      th {
        @include fc; } }

    // thead.fixed
 }    //   position: fixed

  @include max(990) {
    tr {
      @include f; }

    th {
      width: 40%; }
    td {
      width: 60%; }

    td.small {
      width: 0;
      overflow: hidden;
      @include padding(null 0);
      border: 0; }

    thead tr > * {
      height: 75px;
      white-space: nowrap; } }

  @include max(500) {
    font-size: 16px; }

  @include max(400) {
    font-size: 14px;
    thead tr > * {
      height: 56px; }

    th {
      width: 30%; }
    td {
      width: 70%; }

    th,td {
      border-right: 1px solid #f7f7f7;
      @include padding(8px 10px); }

    tbody td {
      line-height: 2; } } }
