.anti {
  background-color: $orange;
  color: #fff; }



.info {
  .border-bottom {
    border-bottom: 1px solid #ccc; }

  .multi-box {
    padding-left: 18px; }

  .table-border {
    width: 100%;
    max-width: 1000px;
    td,
    th {
      border: 1px solid $orange;
      text-align: center;
      width: 20%;
      @include padding(null 5px); }
    tr {
      height: 50px; }
    tr.lg {
      height: 85px; } }

  .anti {
    letter-spacing: 2px;
    @include padding(4px 32px);
    border-radius: 5px;
    white-space: nowrap;
    align-self: flex-start;
    line-height: 2;
    text-align: center;
    // background-color: $orange
    // color: #fff
 }    // font-weight: bold

  .mix2 {
    color: rgb(77, 77, 77);
    .anti {
      width: 225px;
      flex-shrink: 0; }
    .row.top {
      align-items: flex-start; } }


  .info-btn {
    display: block;
    @include padding(16px 90px);
    border-radius: 999px;
    max-width: 380px;
    @include margin(null auto);
    @include hoverBgc($orange-dark);
    @include max(480) {
      max-width: 220px;
      @include padding(12px 0px); } }


  @include max(1023) {
    .anti {
      letter-spacing: 0px; }
    .mix2 .anti {
      width: 190px; }

    .row.top {
      flex-direction: column;
      .pad {
        padding-left: 50px; } } }

  @include max(767) {

    .list {
      > li:before {
        top: 11px; }
      span {
        display: block; } }

    .multi-box {
      .row {
        flex-wrap: wrap;
        margin-bottom: 20px; }
      .anti {
        margin-bottom: 8px; } }
    .mix2 {
      padding-left: 0;
      .lh18 {
        text-indent: -44px;
        padding-left: 42px;

        .pad {
          text-indent: 0;
          padding-left: 0; }

        .break {
          text-indent: 0;
          display: block; } } } }
  @include max(350) {
    .mix2 {
      .lh18 {
        text-indent: -42px; } } }


  @include max(480) {
    .list {
      > li:before {
        top: 9px; } } }

  @include max(767) {
    .table-border {
      &, thead, tfoot, tbody {
        display: block; } }

    .table-border tbody {
      @include f;
      tr, td {
        display: block; }
      td {
        width: 100%;
        border: 0; }

      tr,tr.lg {
        height: auto;
        width: 50%; }
      tr {
        border-right: 1px solid $orange;
        @include padding(6px null); }
      tr.lg {
        border-left: 1px solid $orange; } } } }
