
.logo_gradiant {
  overflow: hidden;
  position: relative;
  &:before {
    content: "";
    display: block;
    @include position(absolute, 0 0 null null );
    background: linear-gradient(to right, rgba(256,256,256,0) 0%,rgba(256,256,256,1) 49%,rgba(256,256,256,1) 100%);
    // background-color: red
    @include size(200%, 100%);
    transition: 4s right; }

  &.show:before {
    right: -200%; } }


.nav__pad {
  @include size(100%, 90px); }

#nav {
  z-index: 10;
  @include size(100%, 90px);
  @include position(fixed, 0 null null 0);
  box-shadow: 0 0 20px 0 rgba(black, 0.3);
  @include fc(flex-start);
  @include min(1100) {
    justify-content: center; }
  @include padding(null 20px);
  background-color: #fff; }

.nav__logo {
  @include margin(null 120px null null);
  @include m(1364, 1100) {
    max-width: 25vw; }
  .half {
    display: none; } }


.nav__cover {
  font-size: 18px;
  height: 100%;

  @include fc;

  a {
    @include padding(null 20px null null);
    @include margin(null 20px null null);
    position: relative;
    height: 100%;
    @include fc;
    display: inline-flex;
    transition: 0.3s box-shadow;
    letter-spacing: 2.5px;
    // font-weight: bold
    font-weight: 500; }

  [class^="icon"] {
    color: $orange;
    font-size: 1.5rem;
    margin-right: 6px; }

  .icon-2 {
    margin-bottom: 3px; }

  @include min(1100) {
    a.active,
    a:hover {
      box-shadow: inset 0 -5px 0px $orange;
      transition: 0.3s box-shadow; } }

  a:not(:last-child):after {
    content: "";
    display: block;
    @include position(absolute, 50% 0 null null );
    transform: translateY(-50%);
    height: 40px;
    border-right: 1px solid rgba($orange, 0.4); } }





// +m(1800, 1100)
@include m(1600, 1100) {
  .nav__cover {
    [class^="icon"] {
      display: none; } } }

@include max(1800) {
  .nav__logo {
    margin-right: 70px; } }


@include max(1365) {
  .nav__pad,
  #nav {
    height: 70px; }

  #nav {
    box-shadow: 0 0 10px 0 rgba(black, 0.3); }

  .nav__logo {
    margin-right: 40px; }

  .nav__cover {
    font-size: 1.1rem;
    a {
      padding-right: 12px;
      margin-right: 12px; }

    a:not(:last-child):after {
      height: 25px; } } }

@include max(1099) {
  .nav__logo {
    margin-right: auto;
    .all {
      height: 50px; } }

  .nav__toggle {
    margin-left: 40px;
    z-index: 12;

    .one, .two, .three {
      @include size(20px, 2px);
      background-color: $orange;
      margin: 4px auto;
      backface-visibility: hidden;
      z-index: 12;
      position: relative; } }

  .nav__cover {
    visibility: hidden;
    opacity: 0;
    border: 0px solid rgba($orange, 0.6);
    position: fixed;
    top: 0;
    right: 0;
    @include size(100%);

    background-color: #f9f9f9;
    flex-direction: column;
    align-items: flex-start;
    @include padding(null 40px);
    a {

      height: 60px;
      border-bottom: 1px solid #ddd;
      width: 100%;
      max-width: 600px;

      @include padding(null 20px);
      @include margin(null auto 10px  auto);

      font-size: 1rem;
      justify-content: flex-start; }
    a.active {
      color: $orange;
      border-bottom: 1px solid #ddd; }

    a:after {}
    a:not(:last-child):after {
      display: none; } } }

@include max(500) {
  .nav__pad,
  #nav {
    height: 55px; }

  .nav__logo {
    .half {
      display: block;
      height: 30px; }
    .all {
      display: none; } } }
