

select {
  background-image: url("images/tri.png");
  background-repeat: no-repeat;
  background-position: center right 15px; }


#dashboard {
  z-index: 3;
  margin-top: 40px;
  position: relative; }

.dashboard__row {
  @include f;
  align-items: center; }

.dashboard__main {
  background-color: $orange;
  @include padding(17px 22px 17px 17px);
  @include position(absolute, 0 50% null null );
  transform: translateX(50%);
  width: 100%;
  color: white;
  transition: 0.3s top;

  @include min(1366) {
    width: calc(100vw - 50px);
    max-width: 1400px;

    border-radius: 999px;
    &:after {
      height: 100%;
      content: "";
      @include position(absolute, 0 390px null null );
      display: block;
      border-right: 1px solid #fff;

      // +max(1366)
      @media only screen and (width: 1366px) {
        right: 370px; } } }


  .icon-1 {
    @include margin(null 25px null null );
    &:before {
      font-size: 50px; } }

  .f20 {
    margin-right: auto; }

  select {
    border-radius: 999px;
    background-color: #fff;
    color: $orange;
    width: 315px;
    @include margin(null null null 90px);
    // +m(1366,1366)
    @media only screen and (width: 1366px) {
      margin-left: 70px; }

    border: 0;
    font-size: 18px;
    letter-spacing: 2px;
    // font-weight: bold
    font-weight: 500;
    @include hoverBgc($orange-light);
    cursor: pointer;
    &:hover {

      transition: 0.3s background-color; } }

  a,
  select,
  option {
    @include padding(10px 35px); } }

.dashboard__btn {
  background-color: #fff;
  border-radius: 999px;
  @include f;
  font-weight: 500;
  // font-weight: bold
  a:not(:last-child) {
    border-right: 1px solid $orange; }
  a {
    @include hoverText($orange);
    letter-spacing: 2px; } }



@include max(1365) {
  #dashboard {
    margin-top: 0px; }

  .dashboard__main {
    @include padding(16px null);

    select,
    .dashboard__btn {
      border-radius: 3px; }

    a,
    select,
    option {
      @include padding(8px 10px); }

    select {
      width: 100%;
      margin-left: 15px;
      max-width: 170px;
      font-size: 15px; }

    .icon-1 {
      display: none; } }

  .dashboard__btn {
    font-size: 15px; } }


@include max(767) {
  .dashboard__row {
    flex-wrap: wrap; }

  .dashboard__main {

    .f20 {
      margin-bottom: 12px; }

    a,
    select,
    option {
      @include padding(null 8px); }

    select {
      max-width: 100%;
      margin-bottom: 10px;
      margin-left: auto; } }

  .dashboard__btn {
    max-width: 100%;
    a {
      width: 25%;
      text-align: center; } }

  .dashboard__main .icon-1 {
    @include margin(null 10px null null );
    @include position(absolute, null 0 -50px null );
    background-color: $orange;
    height: 50px;
    @include fc;
    border-bottom-left-radius: 99px;
    border-bottom-right-radius: 99px;
    cursor: pointer;
    box-shadow: 0 6px 6px 0px rgba(black, 0.1);
    &:before {
      font-size: 32px;
      padding-bottom: 5px; } }


  .dashboard__main .icon-1 {
    transform: translateY(-100%);
    opacity: 0;
    transition: 0.3s transform;
    z-index: -1; }

  .dashboard__main.fixed .icon-1 {
    transform: translateY(0%);
    opacity: 1; } }

@include max(350) {
  .dashboard__main {
    @include padding(null 10px); }
  .dashboard__main select,
  .dashboard__btn {
    font-size: 14px; } }




.box-wrapper {
  border: 1px solid rgba($orange, 0.6);
  @include padding(null 40px);

  li.noDot:before {
    opacity: 0; }

  ul.dot {
    // list-style-image: url("./images/dot.svg")
    li {
      padding-left: 18px; }

    li:before {
      content: '';
      display: inline-block;
      @include size(12px);
      background-image: url("./images/dot.png");
      background-size: cover;
      opacity: 0.8;
      margin-right: 8px; } } }


@include max(350) {
  .dashboard__main {
    a {
      @include padding(null 0px); } } }

@include max(767) {
  .box-wrapper {
    @include padding(null 20px);
    font-size: 15px; } }

@include max(350) {
  .box-wrapper {
    @include padding(null 12px);
    font-size: 14px; }

