.register__doctor {
  @include fc(flex-start);
  background-color: $orange-light;
  @include padding(16px);   // height: 80px
  border-radius: 8px;

  .icon-5 {
    font-size: 40px;
    color: $orange;
    line-height: 1; }

  .f20 {
    margin-right: 25px;
    letter-spacing: 2px;
    white-space: nowrap; }

  .wrapper {
    max-width: 500px;
    @include size(100%,48px);
    position: relative; }

  .wrapper.select {
    margin-right: 10px;
    margin-left: auto; }

  .wrapper.input {
    margin-right: 15px; }

  select,
  input {
    @include size(100%);
    color: #777;
    border: 0; }

  button {
    width: 100px;
    color: #fff;
    background-color: $orange; } }



.department__doctor,
.register__doctor {

  .wrapper.input {
    > div {
      height: 100%; } }

  .easy-autocomplete-container {
    z-index: 1;
    position: relative;
    box-shadow: 0px -20px 20px -25px rgba(black, 0.3),
    0px 22px 15px -20px rgba(black, 0.2),
    -20px 0px 15px -22px rgba(black, 0.2),
    20px 0px 15px -22px rgba(black, 0.2) {}
    transform: translateY(8px);


    ul {
      margin: 0;
      padding: 0;
      @include padding(12px 22px 36px);
      display: none;
      // +f
      // flex-wrap: wrap
      background-color: #fff; }
    li {
      display: inline-block;
      list-style: none;
      font-size: 15px;
      margin-right: 23px;
      line-height: 1.8;
      color: #555;
      @include hoverText($orange);
      cursor: pointer; } } }




