.register__doctor {

  @include max(767) {
    flex-wrap: wrap;

    .wrapper {
      max-width: 100%; }

    .wrapper.select {
      @include margin(null 0 10px null);
      width: 100%; }

    .wrapper.input {
      width: calc(100% - 90px);
      margin-right: 10px; }

    button {
      width: 80px; }

    .icon-5 {
      font-size: 28px;
      margin-bottom: 12px; }
    .f20 {
      margin-bottom: 12px; } }

  @include max(400) {
    button,
    .wrapper {
      font-size: 15px;
      height: 40px; } } }



// .register-index

.block__title {
  margin-bottom: 10px;
  color: #333; }

.register__block {
  margin-bottom: 22px;
  background-color: #fff;
  @include f;
  flex-wrap: wrap;
  // align-items: stretch


  @include padding(30px 45px 22px);

  li {
    @include margin(null 7px 8px);
    width: calc((100% - 70px)/5); }

  a {
    @include fc;
    border-radius: 6px;
    white-space: normal;
    border: 1px solid #cccccc;
    @include size(100%);
    @include padding(10px 5px);
    color: #555;
    line-height: 1.8;
    text-align: center;
    transition: 0.3s background-color, 0.3s color, 0.3s border-color; }

  a:hover {
    transition: 0.3s background-color, 0.3s color, 0.3s border-color;
    background-color: $orange;
    color: #fff;
    border-color: $orange; } }

.register__block {
  @include max(1366) {
    @include padding(null 20px null );
    a {
      font-size: 15px; } }

  @include max(1200) {
    li {
      width: calc((100% - 56px)/4);
      &.tow_unit {
        width: calc((100% - 28px)/2); } } }

  @include max(767) {
    li {
      width: calc((100% - 16px)/2);
      @include margin(null 4px null);
      &.tow_unit {
        width: calc(100% - 8px); } } }

  @include max(480) {
    a {
      line-height: 1.2; }
    li {
      // width: calc(100% - 8px)
      width: 100%;
      @include margin(null 0px null);
      &.tow_unit {
        width: 100%; } } } }




.register__block {
  li {
    transition: 0.5s opacity; }

  .more {
    display: none;
    background-color: #f9f9f9;
    text-indent: 23px; } }

.register__block.cut {
  li:nth-child(4) ~ li {
    height: 0;
    margin: 0;
    opacity: 0;
    overflow: hidden;
    transition: 0.5s opacity; }

  li:last-child.more {
    height: 0;
    opacity: 1;
    display: flex;
    height: auto; } }
